/* radio and checkbox style */
$Radio-size: 22px;
input[type="radio"].check,
input[type="checkbox"].check {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  outline: none;
  border: none;
  cursor: pointer;
  height: $Radio-size;
  width: $Radio-size;
  display: inline-block;
  position: relative;
  z-index: 1000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  color: #fff;
  background: #cbd1d8;
  transition: all 0.15s ease-out 0s;
  & + label {
    cursor: pointer;
  }

  &:hover {
    background: #9faab7;
  }

  &:checked {
    background: var("radio");

    & + label {
      color: var("radio");
    }
  }

  &:checked::before {
    content: "\2713";
    height: $Radio-size;
    width: $Radio-size;
    display: inline-block;
    position: absolute;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    line-height: $Radio-size;
  }

  &:checked::after {
    content: "";
    background: var("radio");
    display: block;
    position: relative;
    z-index: 100;
  }
}

input[type="checkbox"].check {
  border-radius: 2px;
}

input[type="radio"].check {
  border-radius: 50%;

  &::after {
    border-radius: 50%;
  }
}

/* radio and checkbox style end---- */
