// Base
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  background-repeat: no-repeat;
  vertical-align: middle;
  -webkit-font-smoothing: antialiased;
}

html {
  // overflow-y: overlay;
  text-rendering: optimizeLegibility;
  /* defines 1 rem = 62.5% of browser default rem which is 16px
  this leads to 1 rem = 62.5% of 16px = 10px to could make every font and size with rem and this is for easy responsive */
  //want: 1rem = 12px => 12/16px(base) = 75%     | @(big-desktop)
  //want: 1rem = 10px => 10/16px(base) = 62.5%   | @(desktop)
  //want: 1rem = 9px  => 9/16px(base) = 56.25%   | @(tab-land)
  //want: 1rem = 8px  => 8/16px(base) = 50%      | @(tab-port)
  font-size: 62.5%;
  @include respond(big-desktop) {
    font-size: 75%;
  }

  @include respond(tab-land) {
    font-size: 56.25%;
  }

  @include respond(tab-port) {
    font-size: 55%;
  }
}

body {
  box-sizing: border-box;
  transition: all 700ms $cubic;
  background: var(primary);
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: var(radio);
}
::selection {
  color: var(txt);
  background: var(primary);
}

